/* eslint-disable no-unused-vars */
import * as React from "react";
import { useSpring, animated } from "@react-spring/web";
import { usePrevious } from "./use-previous";
import { interpolateZoom } from "d3-interpolate";
import { width, height } from "./dimensions";

import type { ZoomView } from "./data-types";

export function ZoomContainer({ children }) {
  const [view, setView] = React.useState<ZoomView>([
    width / 2,
    height / 2,
    width,
  ]);
  const prevView = usePrevious(view);

  const zoomInterpolator = interpolateZoom.rho(1.6)(prevView ?? view, view);

  function getTransformString(t) {
    const interpolatedView = zoomInterpolator(t);

    const k = width / interpolatedView[2]; // scale
    const translate = [
      width / 2 - interpolatedView[0] * k,
      height / 2 - interpolatedView[1] * k,
    ];

    return `translate(${translate}) scale(${k})`;
  }

  // create a spring that maps from t = 0 (start animation) to t = 1 (end of animation)
  const { t } = useSpring({
    from: { t: 0 },
    to: { t: 1 },
    immediate: false,
    reset: true,
  });

  const childrenProps = React.Children.map(children, (child) => child.props);

  // it's kind of a lil bootleg Context
  const childrenWithSetter = React.useMemo(
    () =>
      React.Children.map(children, (child) => {
        const { props } = child;
        return typeof child.type === "string"
          ? child // ignore DOM Elements
          : React.cloneElement(child, { ...props, setView }); // add 'setView' to props
      }),
    childrenProps
  );

  return (
    <>
      <svg
        viewBox={`0 0 ${width} ${height}`}
        fontFamily="sans-serif" // TODO nicer font
        vectorEffect="non-scaling-stroke"
        height="100vh"
        // overflow="visible" // TODO use margins
      >
        <animated.g transform={t.to(getTransformString)}>
          {childrenWithSetter}
        </animated.g>
      </svg>
    </>
  );
}
